import React from 'react'
import { schedule } from '../weeks'
import SetsColumn from './sets-column.component'
import RowsText from './rows-text.component'
import Navigation from './navigation.component'


const Week = ({ match, location }) => {
    const currentPushups = new URLSearchParams(location.search).get('pushups')
    const { week, day } = match.params
    const scheduleForThisWeek = schedule[week]
    const scheduleForToday = scheduleForThisWeek[day]

    return (
        <div>
            <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                <h1>Week {week}</h1>
                <h3>{scheduleForToday.name}</h3>
                <h4>Rest {scheduleForToday.setsRest} seconds</h4>

                <div style={{ display: 'flex', flexDirection: 'row' }}>
                    <div>
                        <RowsText />
                    </div>
                    {
                        scheduleForToday.groups.map(group => {
                            return (
                                <React.Fragment>
                                    <SetsColumn group={group} />
                                </React.Fragment>
                            )
                        })
                    }
                </div>

                <Navigation
                    currentWeek={week}
                    currentDay={day}
                />
            </div>
        </div>
    )
}

export default Week