import React from 'react'


const SetsColumn = ({ group }) => {

    return (
        <div style={{ padding: '20px' }}>
            {group.currentSets}
            <hr />
            <div>
                {group.setsToDo.map(set => (
                    <div>
                        {set.number}
                    </div>
                ))}
            </div>
        </div>
    )
}

export default SetsColumn