import React from 'react'
import { withRouter } from 'react-router-dom'


const Navigation = ({ history, currentWeek, currentDay }) => {

    const nextStep = () => {
        const week = Number(currentWeek)
        const day = Number(currentDay)
        if (day === 3) {
            return `/${week + 1}/1`
        }

        return `/${week}/${day + 1}`
    }

    return (
        <div>
            <button onClick={() => history.push(nextStep())}>Next Week</button>
        </div>
    )
}

export default withRouter(Navigation)